import * as React from "react";
import Tb from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";

function createData(name, calories, fat, carbs, protein, method) {
  return { name, calories, fat, carbs, protein, method };
}

const rows = [
  createData("Frozen yoghurt", 159, 6.0, 24, 4.0, 1, 2),
  createData("Ice cream sandwich", 237, 9.0, 37, 4.3, 1, 2),
  createData("Eclair", 262, 16.0, 24, 6.0, 1, 2),
  createData("Cupcake", 305, 3.7, 67, 4.3, 1, 2),
  createData("Gingerbread", 356, 16.0, 49, 3.9, 1, 2)
];

export default function Table() {
  return (
    <TableContainer component={Paper}>
      <Tb sx={{ minWidth: 650 }} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell className="tableCell">Tracking ID</TableCell>
            <TableCell className="tableCell">Product</TableCell>
            <TableCell className="tableCell">Customer</TableCell>
            <TableCell className="tableCell">Date</TableCell>
            <TableCell className="tableCell">Amount</TableCell>
            <TableCell className="tableCell">Payment Method</TableCell>
            <TableCell className="tableCell">Status</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.name}>
              <TableCell>{row.name}</TableCell>
              <TableCell className="tableCell">{row.calories}</TableCell>
              <TableCell className="tableCell">{row.fat}</TableCell>
              <TableCell className="tableCell">{row.carbs}</TableCell>
              <TableCell className="tableCell">{row.protein}</TableCell>
              <TableCell className="tableCell">{row.method}</TableCell>
              <TableCell className="tableCell">{row.protein}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Tb>
    </TableContainer>
  );
}
