import "./widget.scss";
import KeyboardArrowUpIcon from "@mui/icons-material/KeyboardArrowUp";
import PersonIcon from "@mui/icons-material/Person";
import AttachMoneyIcon from "@mui/icons-material/AttachMoney";
import FilterFramesIcon from "@mui/icons-material/FilterFrames";
import AccountBalanceWalletIcon from "@mui/icons-material/AccountBalanceWallet";

export default function Widget({ type }) {
  let data;

  // temp
  const amount = 100;
  const diff = 20;

  switch (type) {
    case "user":
      data = {
        title: "USERS",
        isMoney: false,
        link: "See all users",
        icon: <PersonIcon className="icon" style={{ color: "purple" }} />
      };
      break;
    case "order":
      data = {
        title: "ORDERS",
        isMoney: false,
        link: "View all orders",
        icon: <AttachMoneyIcon className="icon" style={{ color: "cremson" }} />
      };
      break;
    case "earnings":
      data = {
        title: "EARNINGS",
        isMoney: false,
        link: "View all users",
        icon: <FilterFramesIcon className="icon" style={{ color: "green" }} />
      };
      break;
    case "balance":
      data = {
        title: "BALANCE",
        isMoney: false,
        link: "View details",
        icon: (
          <AccountBalanceWalletIcon
            className="icon"
            style={{ color: "purple" }}
          />
        )
      };
      break;
    default:
      break;
  }

  return (
    <div className="widget">
      <div className="left">
        <span className="title">{data.title}</span>
        <span className="counter">
          {data.isMoney && "$"} {amount}
        </span>
        <span className="link">{data.link}</span>
      </div>
      <div className="right">
        <div className="percentage positive">
          <KeyboardArrowUpIcon />
          {diff}
        </div>
        {data.icon}
      </div>
    </div>
  );
}
